	module.exports = {"data":{"markdownRemark":{"html":"<h2>Background Size</h2>\n<p>The background-size property specifies the size of the background images. You can set a length or a percentage, with the first value being the width and the second one being the height. You can also use one of the 5 keyword values:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.auto</span> <span class=\"token punctuation\">{</span><span class=\"token property\">background-size</span><span class=\"token punctuation\">:</span> auto<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">.cover</span> <span class=\"token punctuation\">{</span><span class=\"token property\">background-size</span><span class=\"token punctuation\">:</span> cover<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">.contain</span> <span class=\"token punctuation\">{</span><span class=\"token property\">background-size</span><span class=\"token punctuation\">:</span> contain<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">.initial</span> <span class=\"token punctuation\">{</span><span class=\"token property\">background-size</span><span class=\"token punctuation\">:</span> initial<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">.inherit</span> <span class=\"token punctuation\">{</span><span class=\"token property\">background-size</span><span class=\"token punctuation\">:</span> inherit<span class=\"token punctuation\">}</span>\n <span class=\"token comment\" spellcheck=\"true\">/* Percentage and pixel can also be used */</span>\n<span class=\"token selector\">.pixel</span> <span class=\"token punctuation\">{</span><span class=\"token property\">background-size</span><span class=\"token punctuation\">:</span> 50px 50px<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n<span class=\"token selector\">.percentage</span> <span class=\"token punctuation\">{</span><span class=\"token property\">background-size</span><span class=\"token punctuation\">:</span> 50% 50%<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p>Documentation: <a href='https://developer.mozilla.org/en-US/docs/Web/CSS/background-size' target='_blank' rel='nofollow'>MDN</a></p>\n<p>Browser Support: <a href='http://caniuse.com/#search=background-size' target='_blank' rel='nofollow'>caniuse</a></p>","fields":{"slug":"/css/background-size/"},"frontmatter":{"title":"Background Size"}}},"pathContext":{"slug":"/css/background-size/"}}

